//skip nav link

.app-skipnav {
  font-size: 1.06rem;
  line-height: 1.5;
  color: #005ea2;
  text-decoration: underline;
  background: transparent;
  left: 0;
  padding: 0.5rem 1rem;
  position: absolute;
  top: -3.8rem;
  transition: 0.15s ease-in-out;
  z-index: 100;

  &:visited {
    color: #54278f;
  }

  &:hover {
    color: #1a4480;
  }

  &:active {
    color: #162e51;
  }

  &:focus {
    outline: 0.25rem solid #2491ff;
    outline-offset: 0;
    background: white;
    left: 0;
    position: absolute;
    top: 0;
    transition: 0.15s ease-in-out;
  }

  &__container {
    position: relative;
  }
}
